.overlay {
  display: flex;
  flex-direction: row;
  max-width: 90%;
  flex-wrap: wrap;
  margin: auto;
  padding: 5rem 0;
  gap: 20px;
  align-items: flex-start;
}
@media (max-width: 700px) {
  .overlay {
    flex-direction: column;
    justify-content: flex-start;
    align-items: initial;
  }
}
